<template>
  <div class="header">
    <div class="content-wrap">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar" alt="">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{ seller.name }}</span>
        </div>
        <p class="description">{{ seller.description }}/{{ seller.deliveryTime }}分钟送达</p>
        <!--这里如果把v-if去掉 会报错说” 0 is undefined “ 原因是我们在一开始就把seller传了进来，但获取数据是一个异步加载的过程，也就是说
        在初始化时，seller为空，此时再取suport[0]，则会报错，因此要加上这条v-if判断，当seller为空时不解析-->
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{ seller.supports[0].description }}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="support-count" @click="showDetail">
        <span class="count">{{ seller.supports.length }}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bullet" @click="showDetail">
      <span class="bullet-title"></span><span class="bullet-text">{{ seller.bulletin }}</span><span class="icon-keyboard_arrow_right"></span>
    </div>
    <div class="background">
      <img :src="seller.avatar" alt="背景图" width="100%" height="100%">
    </div>
    <transition name="fade">
      <div v-if="detailShow" class="detail">
        <div class="detail-wrap clearfix">
          <div class="detail-main">
            <h1 class="name">{{ seller.name }}</h1>
            <div class="star-wrap">
              <star :size="48" :score="seller.score"></star>
            </div>
            <div class="detail-title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
            <ul v-if="seller.supports" class="supports">
              <li class="support-item" v-for="(item,index) in seller.supports">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{ seller.supports[index].description }}</span>
              </li>
            </ul>
            <div class="detail-title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{ seller.bulletin }}</p>
            </div>
          </div>
        </div>
        <div class="detail-close">
          <i class="icon-close" @click="closeDetail"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import  star from '../star/star'

    export default {
      components: {
        star
      },
      props: {
        seller: {
          type: Object
        }
      },
      data() {
        return {
          detailShow: false
        }
      },
      methods: {
        showDetail() {
           this.detailShow = true;
        },
        closeDetail() {
          this.detailShow = false;
        }
      },
      created() {
        this.classMap = ["decrease", "discount", "special", "invoice", "guarantee"]  //该数组顺序要与db.json中suppoort数组的顺序一样
      }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
  @import "../../common/less/mixin";

  .header{
    position: relative;
    overflow: hidden;   //背景图使用filter：blur()设置模糊度后，如果模糊边缘超出容器，可设置overflow：hidden
    color: #fff;
    background: rgba(7, 17, 27, 0.5);
    .content-wrap{
      padding: 24px 12px 18px 24px;
      position: relative;
      font-size: 0;
      .avatar{
        display: inline-block;      //两个块级元素同时设定dispaly：inline-block时能同行排列，但中间会存在一条缝隙，可通过对父级设置font-size：0来解决
        vertical-align: top;
        img{
          border-radius: 2px;
        }
      }
      .content{
        display: inline-block;
        margin-left: 16px;
        .title{
          margin: 2px 0 8px 0;
          .brand{
            display: inline-block;
            vertical-align: top;
            width: 30px;
            height: 18px;
            .bg-image("brand");
            background-size: 30px 18px;
          }
          .name{
            margin-left: 6px;
            font-size: 16px;
            line-height: 18px;
            font-weight: bold;
          }
        }
        .description{
          margin-bottom: 10px;
          font-size: 12px;
          line-height: 12px;
        }
        .support{
          .icon{
            display: inline-block;
            vertical-align: top;
            width: 12px;
            height: 12px;
            margin-right: 4px;
            background-size: 100% 100%;
            background-repeat: no-repeat;
            &.decrease{
              .bg-image('decrease_1');
            }
            &.discount{
              .bg-image('discount_1');
            }
            &.guarantee{
              .bg-image('guarantee_1');
            }
            &.invoice{
              .bg-image('invoice_1');
            }
            &.special{
              .bg-image('special_1');
            }
          }
          .text{
            line-height: 12px;
            font-size: 12px;
          }
        }
      }
      .support-count{
        position: absolute;
        right: 12px;
        bottom: 14px;
        padding: 0 8px;
        height: 24px;
        line-height: 26px;
        border-radius: 14px;
        background: rgba(0,0,0,0.2);
        text-align: center;
        .count{
          font-size: 10px;
          vertical-align: top;
        }
        .icon-keyboard_arrow_right{
          margin-left: 2px;
          font-size: 10px;
          line-height: 26px;
        }
      }
    }
    .bullet{
      position: relative;
      height: 28px;
      line-height: 28px;
      padding: 0 22px 0 12px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      background: rgba(7,17,27,0.2);
      .bullet-title{
        display: inline-block;
        vertical-align: top;
        margin-top: 8px;
        width: 22px;
        height: 12px;
        .bg-image('bulletin');
        background-size: 22px 12px;
        background-repeat: no-repeat;
      }
      .bullet-text{
        vertical-align: middle;
        margin: 0 4px;
        font-size: 10px;
      }
      .icon-keyboard_arrow_right{
        position: absolute;
        right: 12px;
        top: 5px;
      }
    }
    .background{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;    //z-index: -1 很重要
      filter: blur(10px);
    }
    .fade-enter-active, .fade-leave-active {
      transition: all .5s;
    }
    .fade-enter, .fade-leave-to {
      opacity: 0;
    }
    .detail{
      position: fixed;
      top: 0;
      left: 0;
      z-index: 100;
      width: 100%;
      height: 100%;
      overflow: auto;
      background: rgba(7, 17, 27, 0.8);
      backdrop-filter: blur(10px);  //这条样式只针对iphone手机，设置背景模糊度，安卓手机上无效
      .detail-wrap{
        min-height: 100%;
        width: 100%;
        .detail-main{
          margin-top: 40px;
          padding-bottom: 64px;
          .name{
            text-align: center;
            font-size: 16px;
            line-height: 16px;
            font-weight: 700;
          }
          .star-wrap{
            margin-top: 18px;
            padding: 2px 0;
            text-align: center;
          }
          .detail-title{
            display: flex;
            width: 80%;
            margin: 28px auto 24px;
            .line{
              flex: 1;
              position: relative;
              top: -6px;
              border-bottom: 1px solid rgba(255,255,255,0.2);
            }
            .text{
              padding: 0 12px;
              font-weight: 700;
              font-size: 14px;
            }
          }
          .supports{
            width: 80%;
            margin: 0 auto;
            .support-item{
              padding: 0 12px;
              margin-bottom: 12px;
              font-size: 0;
              &:last-child{
                margin-bottom: 0;
              }
              .icon{
                display: inline-block;
                width: 16px;
                height: 16px;
                vertica-align: top;
                margin-right: 6px;
                background-size: 16px 16px;
                background-repeat: no-repeat;
                &.decrease{
                  .bg-image('decrease_2');
                }
                &.discount{
                  .bg-image('discount_2');
                }
                &.guarantee{
                  .bg-image('guarantee_2');
                }
                &.invoice{
                  .bg-image('invoice_2');
                }
                &.special{
                  .bg-image('special_2');
                }
              }
              .text{
                line-height: 16px;
                font-size: 12px;
              }
            }
          }
          .bulletin{
            width: 80%;
            margin: 0 auto;
            .content{
              padding: 0 12px;
              line-height: 24px;
              font-size: 12px;
              text-align: justify;
            }
          }
        }
      }
      .detail-close{
        position: relative;
        width: 32px;
        height: 32px;
        margin: -64px auto 0 auto;
        clear: both;
        font-size: 32px;
      }
    }
  }
</style>



